import React, {useState, useEffect, useMemo, memo, useRef} from "react";
import {useDrag, useDrop} from "react-dnd";
import {Box, Typography} from '@material-ui/core'
import {makeStyles} from "@material-ui/core/styles";

import clsx from "clsx";


const useStyles = makeStyles(theme => (
    {
        placeBidBackGroundFill:{
            backgroundImage: `url(${process.env.PUBLIC_URL + "/img/auctionButtons/Place_bid_green.svg"})`,

        },
        placeBid: {
            // backgroundImage: `url(${process.env.PUBLIC_URL + "/img/auctionButtons/Place_bid_green.svg"})`,
            backgroundPosition: 'center',
            backgroundSize: '100%',
            backgroundRepeat: 'no-repeat',
            alignContent: "center",
            borderRadius: 8,
            width: "240px",
            height: "60px",
        },
    }));


const PlaceBid = ({G, item, name , idCard,children,placeBid,setStartBid,presentBid,setPresentBid}) => {

    const classes = useStyles(item);
    // const [value, setValue] = useState(G.companyDeck[idCard-1].actualValue);

    const ref = useRef(null);

    const [, drop] = useDrop(

        {
            accept: 'bid',
            drop(_item){
            // setStartBid(G.companyDeck[idCard-1].actualValue)
            setPresentBid(presentBid+1)

            },


            collect: (monitor) => ({
                isOver: monitor.isOver(),
                isOverCurrent: monitor.isOver(),

            }),
        }

    );




    drop(ref);
    return (
        <div ref={ref}>
            <Box
                className={placeBid == 1 ? clsx(classes.placeBid, classes.placeBidBackGroundFill) : null}>

            </Box>

            {children}
        </div>

    );
};
export default PlaceBid;